import React from 'react'
import Header from '../components/Header'
import Sidebar from '../components/Sidebar'
import Spinner from '../components/Spinner'
import PageHeader from '../components/PageHeader'
import axios from 'axios';

const breadcrumb = [
    { name: 'Dashboard', link: '/' },
    { name: 'Cadastrar Atividade' },
]

class CreateTask extends React.Component {

    state = {
        nomeDaAtividade: null,
        nomeDaAtividadeClass: 'form-control',
        selectTipo: 'Série',
        descAtividade: '',
        dataCriacao: Date.now(),
        loading: false
    }    

    handleTaskName = (e) => {
        this.setState({ nomeDaAtividade: e.target.value })
    }

    handleType = (e) => {
        this.setState({ selectTipo: e.target.value })
    }
    handleDesc = (e) => {
        this.setState({ descAtividade: e.target.value })
    }

    handleData = (e) => {
        this.setState({ dataCriacao: e.target.value });

    }

    handleSubmit = async (e) => {
        e.preventDefault();
        let nameOK = false;
        // Valida nome da atividade
        if (this.state.nomeDaAtividade && this.state.nomeDaAtividade !== '') {
            nameOK = true;
            this.setState({ nomeDaAtividadeClass: 'form-control is-valid' })
        }
        else this.setState({ nomeDaAtividadeClass: 'form-control is-invalid' })

        if (nameOK) {
            this.setState({ loading: true })
            // axios
            try {
                await axios.post('http://localhost:3900/api/tasks', {
                    name: this.state.nomeDaAtividade,
                    description: this.state.descAtividade,
                    taskType: this.state.selectTipo,
                    dueDate: this.state.dataCriacao
                },
                    {
                        headers: {
                            "x-auth-token": localStorage.getItem("x-auth-token")
                        }
                    });

                this.props.history.push('/');

            } catch (e) {
                console.log(e.response)
                this.setState({ loading: false })
            }
        }
    }    

    render() {
        return (
            <div>
                <Header />
                <Sidebar />
                <div className="dashboard-wrapper">
                    <div className="container-fluid dashboard-content">
                        <PageHeader title="Criar Atividade" breadcrumb={breadcrumb} />

                        <div className="row">
                            <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
                                <div className="card">
                                    <h5 className="card-header">Preencha o formulário a seguir:</h5>
                                    <div className="card-body">
                                        <form onSubmit={this.handleSubmit}>
                                            <div className="row">
                                                <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12 ">

                                                    <label htmlFor="input-select">Tipo</label>
                                                    <select onChange={this.handleType} className="form-control" id="input-select">
                                                        <option value='Série'>Série</option>
                                                        <option value='Filme'>Filme</option>
                                                        <option value='Livro'>Livro</option>
                                                    </select>

                                                </div>
                                                <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12 ">
                                                    <label htmlFor="title">Nome da Atividade</label>
                                                    <input type="text" className={this.state.nomeDaAtividadeClass} onChange={this.handleTaskName} id="title" placeholder="Nome" />
                                                    <div className="valid-feedback">
                                                        Certinho!
                                                    </div>
                                                    <div className="invalid-feedback">
                                                        Preecha um título!
                                                    </div>
                                                </div>
                                                <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12 ">
                                                    <label htmlFor="descAtividade">Descrição da Atividade</label>
                                                    <input onChange={this.handleDesc} type="text" className="form-control" id="descAtividade" placeholder="Descrição" />
                                                </div>
                                                <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12 ">
                                                    <label htmlFor="datef">Data</label>
                                                    <input onChange={this.handleData} type="date" className="form-control" id="datef" placeholder="Data" />
                                                </div>

                                                <div style={{ marginTop: 20 }} className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12 ">
                                                    {(this.state.loading) ?
                                                        <Spinner />
                                                        : <button className="btn  btn-primary" type="submit"  >Cadastrar Atividade</button>
                                                    }

                                                </div>
                                            </div>
                                        </form>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }


}

export default CreateTask; 